<template>
  <div class="caseHome">
    <mHeader sidebar="ture" ref="child1" :head-title="$t('birthday.topTitle')"></mHeader>
    <div class="caseTop">
      <caseTop :head-top="$t('birthday.secondTitle')"></caseTop>
    </div>
    <div class="caseContent">
      <ul>
        <router-link  to="/CaseBirthday/CaseInner" tag="li">
            <img src="../../images/case/birthday/birthday1.jpg" alt="">
            <span>{{$t('birthday.caseTop')}}</span>
            <dl>
              <dt>{{$t('birthday.caseTitle')}}</dt>
              <dd>{{$t('birthday.caseContent')}}</dd>
            </dl>
        </router-link>
        <router-link  to="/CaseBirthday/CaseInner2" tag="li">
          <img src="../../images/case/birthday/birthday1.jpg" alt="">
          <span>{{$t('birthday.case2Top')}}</span>
          <dl>
            <dt>{{$t('birthday.case2Title')}}</dt>
            <dd>{{$t('birthday.case2Content')}}</dd>
          </dl>
        </router-link>
      </ul>
    </div>
    <mFooter/>
    <transition name="router-slid" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  import mHeader from '../../components/header/headerTab.vue'
  import caseTop from '../../components/caseTopScreen.vue'
  import mFooter from '../../components/footer/footer.vue'

  export default {
    data() {
      return {
      };
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods: {
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      }
    },
    components:{
      mHeader,
      caseTop,
      mFooter
    }
  }

</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .caseHome{
    // background: #ffffff;
    .caseTop {
      width: 100%;
      padding-top: .88rem;
    }
    .caseContent{
      background: #fff;
      width: 100%;
      overflow: hidden;
      ul{
        li{
          position: relative;
          width: 100%;
          height: 7.5rem;
          img{
            @include wh(7.5rem,7.5rem);
            position: absolute;
          }
          span{
            position: absolute;
            @include cl;
            background: #ffffff;
            @include wh(3.36rem, .5rem);
            border-radius: .5rem;
            top: .4rem;
            @include sc(.2rem, #000);
            line-height: .5rem;
            text-align: center;
          }
          dl{
            position: absolute;
            bottom: .7rem;
            @include cl;
            width: 5.76rem;
            dt{
              @include sc(.3rem, #fff);
              padding-bottom: .26rem;
              text-shadow: 0px 2px 2px #808080;
            }
            dd{
              @include sc(.2rem, #fff);
              text-align: justify;
              text-shadow: 0px 2px 2px #808080;
            }
          }
        }
      }
    }
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-active {
    opacity: 0;
  }
  .fade-choose-enter-active, .fade-choose-leave-active {
    transition: opacity .5s;
  }
  .fade-choose-leave, .fade-choose-leave-active {
    display: none;
  }
  .fade-choose-enter, .fade-choose-leave-active {
    opacity: 0;
  }
  .router-slid-enter-active, .router-slid-leave-active {
    transition: all .4s;
  }
  .router-slid-enter, .router-slid-leave-active {
    transform: translate3d(2rem, 0, 0);
    opacity: 0;
  }
  .toggle-cart-enter-active, .toggle-cart-leave-active {
    transition: all .3s ease-out;
  }
  .toggle-cart-enter, .toggle-cart-leave-active {
    transform: translateY(100%);
  }
</style>



